@import url(//maxcdn.bootstrapcdn.com/font-awesome/4.2.0/css/font-awesome.min.css);
html {
  background: transparent;
}
.ldbtn {
  cursor: pointer;
  outline: none;
  border-radius: 100%;
  padding: 0 5px 5px 5px;
  border-color: white;
  border-style: solid;
  color: #FFF;
  background-color: transparent;
}

body {
  background: transparent !important;
}

.ldbtn:focus {
  outline: none;
}

.green{
  color: lightgreen;
  border-color: lightgreen;
}

.red {
  color: red;
  border-color: red;
}

html {
  min-height: 20px;
}

.info-msg,
.success-msg,
.warning-msg,
.error-msg {
  margin: 10px 0;
  padding: 10px;
  border-radius: 3px 3px 3px 3px;
}
.info-msg {
  color: #059;
  background-color: #BEF;
}
.success-msg {
  color: #270;
  background-color: #DFF2BF;
}
.warning-msg {
  color: #9F6000;
  background-color: #FEEFB3;
}
.error-msg {
  color: #D8000C;
  background-color: #FFBABA;
}
